const { render, useState, useEffect } = wp.element;

const FoodMenu = () => {

  useEffect(function(){
  fetch('//wprestaurant.site//wp-json/wp/v2/food_menu/?per_page=71')
    .then((response) => {
      return response.json();
    })
    .then((myJson) => {
      setFoodmenu(myJson);
    });

  }, []);

  const [foodmenu, setFoodmenu] = useState([]);

  const [mainCategory, setMainCategory] = useState('Lunch');

  const [condition, setCondition] = useState(0);

  let getMainCategories = [];

  foodmenu.forEach(function(item){
    getMainCategories.push(item.acf.dish_main_category);
  });

  getMainCategories = [...new Set(getMainCategories)];

  getMainCategories = getMainCategories.reverse();

  let filteredArray = foodmenu.filter(function(item){
    return item.acf.dish_main_category.indexOf(mainCategory) !== -1;
  });

  let newArray = filteredArray.sort(function(a,b){
    return a.acf.dish_sub_category - b.acf.dish_sub_category;
  });

  let getCategories = newArray.map(function(category){
    return category.acf.dish_sub_category;
  });

  getCategories = [...new Set(getCategories)];

  function updateMainCategory(event){
      let getNewMainCategory = event.target.id;
      setMainCategory(getNewMainCategory);
      setCondition(event.target.getAttribute('data-key'));
      console.log(condition);
  }

  return (
    <div>
      <div className="menu-controls">
        {getMainCategories.map(function(button, index){
          return (
            <div data-key={index} onClick={updateMainCategory} id={button} className={`menu-button ${condition == index ? "active-button" : ""}`}>{button}</div>
          )
        })}
      </div>
      <div className="dishes">
      {getCategories.map(function(menuCategory){
          return (
            <div key={menuCategory.index} className="category-wrapper">
              <div className="category-title">{menuCategory}</div>
                {newArray.map(function(menuItem){
                 if(menuItem.acf.dish_sub_category == menuCategory){
                  return (<div className="menu-item" key={menuItem.index}>
                    <div className="menu-item-title">{menuItem.acf.dish_title}</div>
                    <div className="menu-item-description">{menuItem.acf.dish_description}</div>
                    <div className="menu-item-price">${menuItem.acf.dish_price}</div>
                  </div>)
                 }
                })}
            </div>
          );
        })}
      </div>
    </div>
  );
};
render(<FoodMenu />, document.getElementById(`react-app`));